import React from 'react';
import { Link } from 'react-router-dom';

const Navlink = (props) => {
    let { name, className, id, to } = props;
    //set the active class by default when the page loads initially
    let initialClassName = ' active';
    if (to === '/') {
        className += initialClassName;
    }
    return (
        <Link className={className} id={id} to={to} >{name}</Link>
    )
}

export default Navlink;